body {
  font-family: 'Poppins', sans-serif;
  background-color: rgb(17, 161, 245);
  text-align: center;
}

.App {
  margin: 30px 5px;
  color: rgb(255, 255, 255);
 
}

h1 {
  color: rgb(255, 255, 255);
  margin-top: 10px;
  text-shadow: 1px 1px black;
}

button {
  margin: 5px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

form input, form select, form button {
  margin: 5px;
  padding: 10px;
  border: none;
  border-radius: 5px;
}


form button:hover {
  background-color: rgb(3, 189, 74);
  color: white;
}


button.filter {
  margin: 5px;
}

button.filter:hover {
  color: white;
}

button.filter-all:hover {
  background-color: white;
  color: black;
}

button.filter-completed:hover {
  background-color: rgb(3, 189, 74);
}

button.filter-pending:hover {
  background-color: yellow;
  color: black;
}


form select {
  cursor: pointer;
}

ul {
  text-align: left;
  margin-top: 10px;
  padding: 25px;
}

li {
  padding: 8px 10px;
  margin-bottom: 4px;
  background-color: #fff;
  border-radius: 5px;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: black; 
}

li > span {
  flex-grow: 1;
  cursor: pointer;
}

button.complete {
  margin-left: auto;
}

button.remove {
  margin-left: 10px;
}

button.remove:hover {
  background-color: rgb(218, 68, 68); 
  color: white;
}

button.complete:hover {
  background-color: rgb(3, 189, 74);
  color: white;
}


input[type="date"], input[type="date"]::-webkit-calendar-picker-indicator {
  cursor: pointer; 
} 